.section {
   &__hero {
      padding: 0 0 173px;
      @media (max-width: 768px) {
         padding: 0 0 100px;
      }
      @media (max-width: 400px) {
         padding: 0 0 44px;
      }
      &-submit {
         .hero {
            &__content {
               display: flex;
               justify-content: space-between;
               margin-bottom: 68px;
               @media (max-width: 768px) {
                  margin-bottom: 41px;
               }
               @media (max-width: 480px) {
                  margin-bottom: 21px;
               }
               &:nth-child(4) {
                  margin-bottom: 136px;
                  @media (max-width: 768px) {
                     margin-bottom: 68px;
                  }
                  @media (max-width: 400px) {
                     margin-bottom: 40px;
                  }
               }

               &:nth-child(5) {
                  margin-bottom: 251px;
                  @media (max-width: 768px) {
                     margin-bottom: 150px;
                  }
                  @media (max-width: 480px) {
                     margin-bottom: 75px;
                  }

                  .hero__container {
                     .hero__text {
                        max-width: 393px;
                        width: 100%;
                        @media (max-width: 1254px) {
                           max-width: 320px;
                        }
                        @media (max-width: 768px) {
                           max-width: 230px;
                        }
                        @media (max-width: 400px) {
                           max-width: 124px;
                        }
                     }
                  }
               }
            }
            &__subtitle {
               margin-bottom: 4px;
               @media (max-width: 480px) {
                  margin-bottom: 0px;
               }
            }
            &__title {
               margin-bottom: 282px;
               @media (max-width: 1254px) {
                  margin-bottom: 200px;
               }
               @media (max-width: 992px) {
                  margin-bottom: 136px;
               }
               @media (max-width: 480px) {
                  margin-bottom: 85px !important;
               }
            }
         }
      }
      &-action {
         .hero {
            &__title {
               margin-bottom: 174px;
               @media (max-width: 480px) {
                  margin-bottom: 52px !important;
               }
            }
            &__subtitle {
               text-align: center;
               &:nth-child(4) {
                  margin-bottom: 660px;
                  @media (max-width: 1254px) {
                     margin-bottom: 550px;
                  }
                  @media (max-width: 768px) {
                     margin-bottom: 440px;
                  }
                  @media (max-width: 600px) {
                     margin-bottom: 222px;
                  }
                  @media (max-width: 480px) {
                     margin-bottom: 122px;
                  }
               }
            }
            &__subtext {
               margin-bottom: 30px;
               @media (max-width: 480px) {
                  margin-bottom: 20px;
               }
            }
         }
      }
      &-randomplayer {
         .hero {
            &__subtitle {
               margin-bottom: 513px;
               @media (max-width: 1254px) {
                  margin-bottom: 450px;
               }
               @media (max-width: 768px) {
                  margin-bottom: 340px;
               }
               @media (max-width: 480px) {
                  margin-bottom: 280px;
               }
            }
            &__title {
               @media (max-width: 480px) {
                  margin-bottom: 20px !important;
               }
            }
         }
      }
      &-play {
         .hero {
            &__content {
               &:nth-child(3) {
                  margin-bottom: 125px;
                  @media (max-width: 768px) {
                     margin-bottom: 70px;
                  }
                  @media (max-width: 480px) {
                     margin-bottom: 37px;
                  }
               }
               &:nth-child(4) {
                  flex-direction: column;
                  margin-bottom: 354px;
                  @media (max-width: 1254px) {
                     margin-bottom: 254px;
                  }
                  @media (max-width: 768px) {
                     margin-bottom: 200px;
                  }
                  @media (max-width: 600px) {
                     margin-bottom: 154px;
                  }
                  @media (max-width: 480px) {
                     margin-bottom: 107px;
                  }
                  .hero__subtitle {
                     margin-bottom: 40px;
                     @media (max-width: 480px) {
                        text-align: left;
                        margin-bottom: 12px;
                     }
                  }
                  .hero__items {
                     margin-bottom: 0 !important;
                  }
                  .hero__item {
                     flex: 0 1 33%;
                  }
               }
            }
         }
      }
      &-wait {
         .hero {
            &__title {
               @media (max-width: 600px) {
                  margin-bottom: 85px !important;
               }
            }
            &__subtext {
               color: #ffffff;
               max-width: 400px;
               width: 100%;
               margin: 0 auto 20px;
               @media (max-width: 600px) {
                  max-width: 320px;
               }
               @media (max-width: 480px) {
                  max-width: 240px;
               }
               &:last-child {
                  margin-bottom: 0;
               }
            }
            &__content:nth-child(3) {
               margin-bottom: 268px;
               @media (max-width: 768px) {
                  margin-bottom: 168px;
               }
               @media (max-width: 600px) {
                  margin-bottom: 110px;
               }
               @media (max-width: 480px) {
                  margin-bottom: 80px;
               }
            }
            &__content:nth-child(4) {
               margin-bottom: 32px;
               @media (max-width: 768px) {
                  margin-bottom: 20px;
               }
            }
            &__swiper {
               margin-bottom: 50px;
               @media (max-width: 768px) {
                  margin-bottom: 27px;
               }
            }
         }
      }
      &-result {
         .hero {
            &__title {
               @media (max-width: 480px) {
                  margin-bottom: 45px !important;
               }
            }
            &__content:nth-child(4) {
               margin-bottom: 96px;
               @media (max-width: 768px) {
                  margin-bottom: 36px;
               }
               @media (max-width: 480px) {
                  margin-bottom: 29px;
               }
            }
            &__content:nth-child(5) {
               margin-bottom: 152px;
               @media (max-width: 768px) {
                  margin-bottom: 80px;
               }
               @media (max-width: 480px) {
                  margin-bottom: 45px;
               }
            }
         }
      }
      &-cancel {
         .hero__subtitle {
            margin-bottom: 325px;
            @media (max-width: 992px) {
               margin-bottom: 225px;
            }
         }
      }
      &-home {
         .hero {
            &__title {
               margin-bottom: 100px;
               @media (max-width: 480px) {
                  margin-bottom: 27px;
               }
            }
            h2.hero__title {
               margin-bottom: 40px;
               @media (max-width: 480px) {
                  margin-bottom: 5px !important;
               }
            }
            &__subtext {
               text-align: center;
               font-family: "Jano Sans Pro Regular";
               max-width: 750px;
               width: 100%;
               margin: 0 auto 70px;
               @media (max-width: 768px) {
                  margin-bottom: 40px;
               }
               @media (max-width: 480px) {
                  margin-bottom: 22px;
               }
               &:nth-child(2) {
                  margin: 0 auto 150px;
                  @media (max-width: 768px) {
                     margin: 0 auto 100px;
                  }
                  @media (max-width: 480px) {
                     margin: 0 auto 40px;
                  }
               }
            }
            &__container {
               max-height: 590px;
               height: 100%;
               overflow-y: scroll !important;
               border-bottom: 1px solid #000000;
               padding-bottom: 30px;
               margin-bottom: 70px;
               position: relative;
               padding-right: 20px;
               @media (max-width: 992px) {
                  max-height: 540px;
               }
               @media (max-width: 768px) {
                  max-height: 500px;
                  padding-bottom: 20px;
                  margin-bottom: 30px;
               }
               @media (max-width: 480px) {
                  max-height: 330px;
               }
               @media (max-width: 600px) {
                  padding-right: 0 !important;
               }
               &::-webkit-scrollbar {
                  width: 3px;
                  border-radius: 5px;
                  @media (max-width: 600px) {
                     display: none !important;
                  }
               }
               &::-webkit-scrollbar-track {
                  background: #d9d9d9;
               }
               &::-webkit-scrollbar-thumb {
                  background: rgb(0, 0, 0);
                  border-radius: 5px;
                  cursor: pointer;
               }
            }
            &__table {
               width: 100%;
               tr {
                  margin: 0 !important;
                  border-top: 1px solid #dddddd;
                  border-bottom: 1px solid #dddddd;
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  height: 100%;
                  padding: 26px 0;
                  &:first-child {
                     border-top: none !important;
                  }
                  &:last-child {
                     border-bottom: none !important;
                  }
                  @media (max-width: 480px) {
                     padding: 16px 0;
                  }
               }
               .text,
               .subtext {
                  @media (max-width: 480px) {
                     font-size: 9px;
                  }
               }
               .hero__text {
                  text-align: center;
               }
               .hero__table-btn {
                  padding: 11px 33px;
                  width: auto;
                  display: inline-flex;
                  font-size: 40px;
                  margin-bottom: 0;
                  &.hide {
                     opacity: 0;
                     visibility: hidden;
                  }
                  @media (max-width: 992px) {
                     font-size: 30px;
                  }
                  @media (max-width: 768px) {
                     font-size: 22px;
                  }
                  @media (max-width: 600px) {
                     padding: 11px 22px;
                     font-size: 18px;
                  }
                  @media (max-width: 480px) {
                     font-size: 12px;
                     padding: 11px 13px;
                  }
               }
            }
         }
      }
      &-start {
         .hero {
            &-decor {
               position: relative;
               &::after {
                  content: url(..//img/scissors.webp);
                  position: absolute;
                  left: 0;
                  top: 600px;
                  transform: rotate(66deg) scale(2);
                  @media (max-width: 1254px) {
                     top: 500px;
                  }
                  @media (max-width: 992px) {
                     top: 400px;
                  }
                  @media (max-width: 768px) {
                     top: 300px;
                     transform: rotate(66deg) scale(1.5);
                  }
                  @media (max-width: 600px) {
                     top: 200px;
                  }
                  @media (max-width: 400px) {
                     top: 170px;
                  }
                  @media (max-width: 350px) {
                     left: -20px;
                  }
               }
            }
            &__title {
               @media (max-width: 600px) {
                  margin-bottom: 54px !important;
               }
            }
            &__text {
               color: #817777;
               font-family: "Jano Sans Pro Regular";
               max-width: 592px;
               width: 100%;
               margin: 0 auto 30px;
               text-align: center;
               @media (max-width: 992px) {
                  margin: 0 auto 20px;
               }
               @media (max-width: 480px) {
                  margin: 0 auto 7px;
               }
               @media (max-width: 400px) {
                  font-size: 10px !important;
               }
               &:nth-child(6) {
                  margin: 80px auto 30px !important;
                  @media (max-width: 992px) {
                     margin: 60px auto 20px;
                  }
                  @media (max-width: 768px) {
                     margin: 40px auto 20px !important;
                  }
                  @media (max-width: 480px) {
                     margin: 30px auto 10px !important;
                  }
               }
            }
            &__link {
               text-align: center;
               word-break: break-all;
               display: flex;
               justify-content: center;
               margin-bottom: 80px;
               @media (max-width: 992px) {
                  margin-bottom: 50px;
               }
               @media (max-width: 768px) {
                  margin-bottom: 28px;
               }
               @media (max-width: 400px) {
                  font-size: 10px !important;
               }
            }
            &__subtitle {
               text-align: center;
               margin: 72px 0 130px;
               @media (max-width: 992px) {
                  margin: 52px 0 80px;
               }
               @media (max-width: 768px) {
                  margin: 36px 0 50px;
               }
               @media (max-width: 400px) {
                  font-size: 14px !important;
               }
            }
            &__subtext {
               max-width: 700px;
               margin: 0 auto;
               text-align: center;
               @media (max-width: 400px) {
                  font-size: 10px !important;
               }
               &.subtext {
                  font-family: "Jano Sans Pro Regular";
               }
            }
            &__btn {
               &.btn__gradient {
                  @media (max-width: 480px) {
                     font-size: 12px !important;
                     padding: 15px 0;
                  }
               }
               &.btn {
                  @media (max-width: 480px) {
                     font-size: 18px;
                  }
               }
            }
         }
      }
      &-welcome {
         .hero {
            &__swiper {
               margin-bottom: 64px;
               @media (max-width: 768px) {
                  margin-bottom: 32px;
               }
            }
            &__btn {
               img {
                  transform: scale(2.5);
                  @media (max-width: 1254px) {
                     transform: scale(2);
                  }
                  @media (max-width: 768px) {
                     transform: scale(1.5);
                  }
                  @media (max-width: 480px) {
                     transform: scale(1);
                  }
               }
            }
            &__subtext {
               margin: 0 auto 20px;
               &:nth-child(2) {
                  margin-bottom: 120px;
                  @media (max-width: 768px) {
                     max-width: 350px;
                     margin: 0 auto 64px;
                  }
               }
            }
            &__subtitle {
               color: #817777;
               margin-bottom: 27px;
               @media (max-width: 768px) {
                  margin-bottom: 7px;
               }
            }
            .select {
               position: relative;
               width: 100%;
               margin-bottom: 222px;
               @media (max-width: 768px) {
                  margin-bottom: 111px;
               }
               select {
                  display: none;
               }
               .select-selected {
                  padding: 21px 28px;
                  background: #f2f2f2;
                  border-radius: 10px;
                  transition: all 0.3s ease;
                  &::after {
                     content: url(..//img/arrow.svg);
                     position: absolute;
                     right: 22px;
                     top: 50%;
                     transform: translate(0, -50%);
                     transition: all 0.3s ease;
                  }
               }
               .select-arrow {
                  border-radius: 10px 10px 0 0;
                  transition: all 0.3s ease;
                  &:after {
                     transform: translate(0, -50%) rotate(180deg);
                     transition: all 0.3s ease;
                  }
               }
               div,
               .select-selected {
                  cursor: pointer;
                  user-select: none;
                  color: #716e6e;
                  text-transform: uppercase;
                  @media (max-width: 480px) {
                     font-size: 12px !important;
                  }
               }
               .select-items {
                  position: absolute;
                  top: 100%;
                  left: 0;
                  right: 0;
                  z-index: 99 !important;
                  background: #f2f2f2;
                  border-radius: 0 0 10px 10px;
                  padding: 21px 28px;
                  transform: scaleY(1);
                  transform-origin: top;
                  transition: all 0.3s ease;
                  @media (max-width: 480px) {
                     padding: 10px 28px;
                  }
                  & div {
                     margin-bottom: 21px;
                     &:last-child {
                        margin-bottom: 0;
                     }
                  }
               }
               .select-hide {
                  transform: scaleY(0);
                  transition: all 0.3s ease;
               }
            }
         }
      }
   }
}
.hero {
   &__swiper {
      margin-bottom: 50px;
      @media (max-width: 768px) {
         margin-bottom: 27px;
      }
      .slide {
         display: flex;
         justify-content: center;
         align-items: center;
         filter: blur(7px);
         img {
            animation: rotate 1s linear infinite 0s both;
         }
         @keyframes rotate {
            from {
               transform: rotate(0deg);
            }
            to {
               transform: rotate(360deg);
            }
         }
      }
      .swiper-slide-active {
         filter: blur(0);
         img {
            animation: none;
         }
      }
   }
   &__title {
      margin-bottom: 135px;
      @media (max-width: 600px) {
         margin-bottom: 40px !important;
      }
   }
   &__content {
      margin-bottom: 71px;
      &:nth-child(2) {
         .hero__item {
            &:hover {
               background: #f3f3f3;
               color: #000000;
            }
         }
      }
      &:nth-child(4) {
         .hero__items {
            margin-bottom: 136px !important;
            @media (max-width: 600px) {
               margin-bottom: 70px !important;
            }
            @media (max-width: 480px) {
               margin-bottom: 43px !important;
            }
         }
      }
      @media (max-width: 600px) {
         margin-bottom: 31px;
      }
      @media (max-width: 480px) {
         margin-bottom: 21px;
      }
   }
   &__subtitle {
      margin-bottom: 37px;
      @media (max-width: 600px) {
         margin-bottom: 17px;
      }
      @media (max-width: 480px) {
         margin-bottom: 11px;
      }
   }
   &__count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: #f3f3f3;
      border-radius: 20px;
      padding: 61px 45px 61px 55px;
      margin-bottom: 30px;
      .hero__subtitle {
         margin-bottom: 0;
      }
      @media (max-width: 768px) {
         padding: 41px 25px 41px 35px;
         border-radius: 12px;
      }
      @media (max-width: 480px) {
         padding: 18px 17px;
         border-radius: 6px;
         margin-bottom: 9px;
      }
   }
   &__range {
      width: 100%;
      height: 11px;
      border-radius: 7px !important;
      -webkit-appearance: none;
      background: #f3f3f3;
      background-image: linear-gradient(#000000, #000000);
      background-size: 25% 100%;
      background-repeat: no-repeat;
      margin-bottom: 28px;
      @media (max-width: 600px) {
         margin-bottom: 18px;
      }
      @media (max-width: 480px) {
         margin-bottom: 9px;
         height: 4px;
      }
      &::-webkit-slider-thumb  {
         -webkit-appearance: none;
         appearance: none;
         height: 100%;
         width: 22px;
         background: #000000;
         cursor: ew-resize;
         box-shadow: 0 0 0 0 rgb(0, 0, 0);
         border: none;
      }
      &::-moz-range-thumb {
         -webkit-appearance: none;
         height: 100%;
         width: 22px;
         background: inherit;
         cursor: ew-resize;
         border: none;
         box-shadow: 0 0 0 0 rgb(0, 0, 0);
      }
      &::-ms-thumb {
         -webkit-appearance: none;
         height: 100%;
         width: 22px;
         background: inherit;
         cursor: ew-resize;
         border: none;
         box-shadow: 0 0 0 0 rgb(0, 0, 0);
      }
      &::-webkit-slider-runnable-track {
         -webkit-appearance: none;
         box-shadow: none;
         border: none;
         background: transparent;
         border-radius: 7px;
      }
      &::-moz-range-track {
         -webkit-appearance: none;
         box-shadow: none;
         border: none;
         background: transparent;
         border-radius: 7px;
      }
      &::-ms-track {
         -webkit-appearance: none;
         box-shadow: none;
         border: none;
         background: transparent;
         border-radius: 7px;
      }
   }
   &__sum {
      text-align: right;
      position: relative;
      margin-bottom: 30px;
      @media (max-width: 768px) {
         margin-bottom: 20px !important;
      }
      @media (max-width: 480px) {
         margin-bottom: 9px !important;
      }
   }
   &__items {
      display: flex;
      align-items: center;
      justify-content: center;
   }
   &__item {
      padding: 19px 0;
      margin-right: 26px;
      flex: 0 1 25%;
      display: flex;
      justify-content: center;
      position: relative;
      z-index: 0;
      @media (max-width: 600px) {
         margin-right: 16px;
         padding: 12px 0;
      }
      @media (max-width: 480px) {
         margin-right: 8px;
      }

      &:last-child {
         margin-right: 0;
      }

      .custom-radio > input {
         position: absolute;
         z-index: -1;
         opacity: 0;
      }
      .custom-radio > span {
         display: inline-flex;
         align-items: center;
         user-select: none;
         transition: all 0.3s ease;
         z-index: 99;
      }
      .custom-radio > span::before {
         content: "";
         position: absolute;
         left: 0;
         top: 0;
         bottom: 0;
         right: 0;
         width: 100%;
         height: 100%;
         z-index: -1;
         border: none;
         background: #f3f3f3;
         border-radius: 22px;
         transition: all 0.3s ease;
         @media (max-width: 768px) {
            border-radius: 12px;
         }
         @media (max-width: 480px) {
            border-radius: 6px;
         }
      }
      .custom-radio > input:not(:disabled):not(:checked) + span:hover::before {
         background: #f3f3f3;
         transition: all 0.3s ease;
      }
      .custom-radio > input:not(:disabled):active + span::before {
         background: #000000;
         transition: all 0.3s ease;
      }
      .custom-radio > input:focus + span::before {
         box-shadow: 0 0 0 0 none;
      }
      .custom-radio > input:focus:not(:checked) + span::before {
         background: #000000;
         transition: all 0.3s ease;
      }
      .custom-radio > input:checked + span::before {
         background: #000000;
         transition: all 0.3s ease;
      }
      .custom-radio > input:checked + span {
         color: #f3f3f3;
         transition: all 0.3s ease;
      }
      .custom-radio > input:disabled + span::before {
         background: #000000;
         transition: all 0.3s ease;
      }
   }
}
