.nav {
   padding: 151px 0 138px;
   display: flex;
   align-items: center;
   justify-content: space-between;
   position: relative;
   @media (max-width: 1254px) {
      padding: 100px 0 60px;
   }
   @media (max-width: 992px) {
      padding: 60px 0;
   }
   @media (max-width: 480px) {
      padding: 45px 0 41px;
   }
   &__menu {
      top: -100%;
      transition: all 0.3s ease;
      display: block;
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      z-index: 99;
      background: #ffffff;
      padding: 100px 43px 43px;
      overflow-y: auto;
      &::-webkit-scrollbar {
         display: none;
      }
      &.active {
         transition: all 0.3s ease;
         top: 0;
      }
   }
   &__list {
      list-style: none;
      margin-bottom: 100px;
      li{
         margin-bottom: 15px;
         @media (max-width: 480px){
            margin-bottom: 8px;
         }
         @media (max-width: 400px){
            margin-bottom: 0;
         }
      }
   }
   .burger {
      @media (max-width: 768px) {
         width: 26px;
         height: 20px;
         position: relative;
         z-index: 99;
         &::after,
         &::before,
         span {
            content: "";
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0;
            right: 0;
            background-color: #000000;
            transition: 0.3s ease 0s;
            transform-origin: right;
         }
         &::before {
            top: 0px;
         }
         &::after {
            bottom: 0px;
         }
         span {
            top: 50%;
            transform: translate(0, -50%);
         }
         &.active {
            &::before {
               transform: rotate(-45deg);
               transition: 0.3s ease 0s;
            }
            &::after {
               transform: rotate(45deg);
               transition: 0.3s ease 0s;
            }
            span {
               transform: translate(0, -50%) scaleX(0);
               transition: 0.3s ease 0s;
            }
         }
      }
      @media (max-width: 480px) {
         transform: scale(0.7);
      }
   }
   &__price {
      margin-bottom: 100px;
      @media (max-width: 480px){
         margin-bottom: 50px;
      }
      .subtext{
         display: flex;
         align-items: center;
         justify-content: flex-start;
         margin-top: 5px;
      }
      strong{
         margin: 0 5px;
      }
      img{
         margin-left: 5px;
         @media (max-width: 480px){
            width: 140px;
         }
         @media (max-width: 400px){
            width: 100px;
         }
      }
   }
}
