*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important;
	font-family: "Jano Sans Pro Medium";
	font-style: normal;
	transition: all 0.3s ease;
	.hero__win,
	.hero__lose,
	.hero__draw {
		opacity: 0;
		visibility: hidden;
		height: 0;
		transition: all 0.3s ease;
	}
	&.locked{
		overflow: hidden !important;
	}
	&.win,
	&.lose,
	&.draw {
		transition: all 0.3s ease;
		position: relative;
		&::after {
			content: url(..//img/decor.webp);
			position: fixed;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			transform-origin: center;
			animation: fade 2.5s ease 0s both;
		}
		.countdown,
		.swiper,
		.btn.disable,
		.hero__subtext {
			display: none;
		}
		.hero__container > .hero__text,
		.hero__subtext {
			color: #f3f3f3;
			transition: all 0.3s ease;
		}
	}
	&.win {
		background: #65d053;
		.hero__win {
			opacity: 1;
			visibility: visible;
			height: auto;
			transition: all 0.3s ease;
			.hero__subtext {
				display: block;
			}
		}
	}
	&.lose {
		background: #ff3838;
		.hero__lose {
			opacity: 1;
			visibility: visible;
			height: auto;
			transition: all 0.3s ease;
			.hero__subtext {
				display: block;
			}
		}
	}
	&.draw {
		background: #ffe76b;
		.hero__container > .hero__text,
		.hero__subtext,
		.hero__title {
			color: #000000 !important;
		}
		.hero__draw {
			opacity: 1;
			visibility: visible;
			height: auto;
			transition: all 0.3s ease;
			.hero__subtext {
				display: block;
			}
		}
	}
	&.home {
		.nav {
			padding: 151px 0 0 !important;
			@media (max-width: 1254px) {
				padding: 100px 0 0 !important;
			}
			@media (max-width: 992px) {
				padding: 60px 0 0 !important;
			}
			@media (max-width: 480px) {
				padding: 45px 0 0 !important;
			}
		}
		.nav__text, .burger {
			display: none;
		}
	}
	@keyframes fade {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale(3) translate(-10%, -10%);
			transform-origin: center;
			opacity: 1;
		}
		100% {
			transform: scale(3) translate(-10%, -10%);
			transform-origin: center;
			opacity: 0;
			visibility: hidden;
		}
	}
	@media (max-width: 600px) {
		@keyframes fade {
			0% {
				opacity: 1;
			}
			50% {
				transform: scale(2) translate(-25%, -25%);
				transform-origin: center;
				opacity: 1;
			}
			100% {
				transform: scale(2) translate(-25%, -25%);
				transform-origin: center;
				opacity: 0;
				visibility: hidden;
			}
		}
	}
	@media (max-width: 350px){
		@keyframes fade {
			0% {
				opacity: 1;
			}
			50% {
				transform: scale(1.5) translate(-35%, -35%);
				transform-origin: center;
				opacity: 1;
			}
			100% {
				transform: scale(1.5) translate(-35%, -35%);
				transform-origin: center;
				opacity: 0;
				visibility: hidden;
			}
		}
	}
}

main {
	flex: 1 1 auto;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	color: #000000;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	@media (max-width: $md1) {
		max-width: 946px;
	}
	@media (max-width: $md2) {
		max-width: 750px;
	}
	@media (max-width: $md3) {
		max-width: none;
		padding: 0 43px;
	}
	@media (max-width: 350px) {
		padding: 0 23px;
	}
}
//
.title {
	font-size: 80px;
	line-height: 1.2375;
	color: #000000 !important;
	text-align: center;
	@media (max-width: 1254px) {
		font-size: 68px;
	}
	@media (max-width: 992px) {
		font-size: 48px;
	}
	@media (max-width: 768px) {
		font-size: 36px;
	}
	@media (max-width: 480px) {
		font-size: 30px;
	}
	@media (max-width: 400px) {
		font-size: 22px;
	}
}
a.title {
	color: #000000 !important;
}
.subtitle {
	font-size: 40px;
	line-height: 1.225;
	color: #000000;
	@media (max-width: 1254px) {
		font-size: 30px;
	}
	@media (max-width: 600px) {
		font-size: 22px;
	}
	@media (max-width: 480px) {
		font-size: 14px;
	}
	@media (max-width: 400px) {
		font-size: 9px;
	}
}
.subtitle-big {
	font-size: 50px;
	line-height: 1.24;
	color: #000000;
	@media (max-width: 1254px) {
		font-size: 40px;
	}
	@media (max-width: 992px) {
		font-size: 34px;
	}
	@media (max-width: 768px) {
		font-size: 22px;
	}
	@media (max-width: 600px) {
		font-size: 18px;
	}
	@media (max-width: 480px) {
		font-size: 16px;
	}
	@media (max-width: 400px) {
		font-size: 14px;
	}
}
.text {
	font-size: 30px;
	line-height: 1.23;
	color: #000000;
	@media (max-width: 1254px) {
		font-size: 22px;
	}
	@media (max-width: 768px) {
		font-size: 16px;
	}
	@media (max-width: 480px) {
		font-size: 13px;
	}
	@media (max-width: 400px) {
		font-size: 9px;
	}
}
.subtext {
	font-size: 20px;
	line-height: 1.25;
	text-align: center;
	color: #000000;
	@media (max-width: 600px) {
		font-size: 16px;
	}
	@media (max-width: 480px) {
		font-size: 12px;
	}
	@media (max-width: 400px) {
		font-size: 9px;
	}
}
.btn {
	background: #000000;
	border-radius: 20px;
	font-size: 70px;
	line-height: 1.24;
	color: #ffffff;
	width: 100%;
	padding: 44px 0;
	display: flex;
	justify-content: center;
	margin-bottom: 31px;
	@media (max-width: 1254px) {
		font-size: 60px;
		padding: 30px 0;
	}
	@media (max-width: 992px) {
		font-size: 48px;
	}
	@media (max-width: 768px) {
		font-size: 36px;
		padding: 20px 0;
	}
	@media (max-width: 600px) {
		font-size: 30px;
		margin-bottom: 21px;
		border-radius: 6px;
	}
	@media (max-width: 480px) {
		padding: 12px 0;
		font-size: 20px;
		margin-bottom: 15px;
	}
	&__grey {
		background: #a4a4a4;
	}
	&__cancel {
		background: #ffffff;
		border: 1px solid #ececec;
		color: #f40000;
	}
	&__blue {
		background: #067dc0;
	}
	&.disable {
		background: #f0f0f0;
		color: #d8d8d8;
	}
	&__gradient {
		background: linear-gradient(180deg, #462a69 0%, #090d6c 100%);
	}
}
